.profile__avatar-edit {
    padding: 0;
    line-height: 0;
    border: none;
    width: 100%;
    background: inherit;
    position: relative;
    cursor: pointer;
}

.profile__avatar-edit:after {
    background: rgba(0, 0, 0, 0.8) url('../../../images/avatar-edit.svg') center no-repeat;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 0.2s;
}

.profile__avatar-edit:hover::after {
    opacity: 1;
}